.block {
    max-width: 350px;
    width: 100%;
    display: flex;
    flex-direction: column;

    gap: 20px;
}

.field {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.label {
    font-family: "IBM Plex Mono";
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    margin-bottom: 5px;

    color: var(--color-font-secondary);
}
