.box {
    width: 100%;
    height: 40px;

    background-color: var(--color-background-field);
    border: 1px solid var(--color-border-field);
    border-radius: 4px;

    font-family: "IBM Plex Mono";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 21px;
}

.text {
    height: 100%;
    width: 100%;
    background-color: transparent;
    padding: 0 12px;
}

.text::placeholder {
    color: var(--color-font-placeholder);
}
