.link {
    padding: 12px;

    display: flex;
    align-items: center;
}

.link > img {
    width: 30px;
    filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(265deg) brightness(106%) contrast(105%);
}

.link:hover > img {
    filter: invert(63%) sepia(0%) saturate(15%) hue-rotate(156deg) brightness(105%) contrast(69%);
}
